/* All base styles */

@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300&family=Poppins:wght@300&family=Roboto:wght@300&family=Rye&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: 0;
    list-style: none;
    text-decoration: none;

}
/* color var */
:root {
    --color-bg: #9292f5;
    --color-bg-variant: #7998ff;
    --color-primary:#2c2c6e;
    --color-primary-variant: rgba(33, 6, 107, 0.4);
    --color-white: #fff;
    --color-light: rgba(10, 218, 233, 0.932);

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
    --transition: all 400ms ease;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/black-felt.png)
}

/* Styles for each component */

.container {
    width: var(--container-width-lg);
    margin: 0 auto;

}

h1, 
h2, 
h3, 
h4, 
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    height: 80vh;
}

section > h2, 
section > h5 {
    text-align: center;
    color: var(--color-light);
}

section >h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(-transition);
}

a:hover {
    color: var(--color-white);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: zoom-in;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width:  100%;
    object-fit: cover;
}

/* media queries(tablets) */

@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
    
}

/* Media Queries phones/small devices */

@media screen and (max-width: 600px) {
    .container{
        width: var(--container-width-sm);
    }

    section {
        margin-bottom: 2rem;
    }
    
}