nav {
    background: rgba(255,255,255, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 1rem;
    border-radius: flex;
    /* color: var(--color-light); */
    font-size: 1.1rem;

}

nav a:hover {
    background: rgba(255, 255, 255, 0.3);
}

nav a.active{
    background: var(--color-bg);
    color: var(--color--white);
}